.banner{
    background-size: cover;
    height: 448px;
    color: White;
    margin-bottom: 20px;
    @media only screen and (max-width: 480px) {
        width: 100%;
        object-fit: cover;
        object-position: right;
        }
}
.content{
    padding-top: 140px;
    height: 200px;
    padding-left: 15px;
    
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    cursor: pointer;
    margin-right: 1rem;
}
.button:hover{
    color: black;
    background-color: #e6e6ee;
}
.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
}
@media only screen and (max-width:600px){.description{max-width: 300px;
    overflow: hidden;
}}
.fade_bottom{
    height: 16rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.6),#111);
}