.row{
    margin-left: 20px;
    color: #fff;
}
.rowposts{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.rowposts::-webkit-scrollbar{
    display: none;
}
.posts{
    max-height: 150px;
    margin-right: 10px;
}
.smallposts{
    max-height: 150px;
    margin-right: 10px;
}
.movie-title{
    font-family: inherit;
    font-size: 15px;
}